@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap");
@import "./styles/global.scss";

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
html {
  scroll-behavior: smooth;
}
body {
  font-family: "Inter", sans-serif;
  font-size: 16px;
  line-height: 24px;
}

body::-webkit-scrollbar {
  width: 10px;
  height: 5px;
}

body::-webkit-scrollbar-track {
  background: #f9fafb;
  border-radius: 12px;
}

body::-webkit-scrollbar-thumb {
  background: #d1d5db;
  border-radius: 12px;
}

.app {
  min-height: 100vh;
  @include theme() {
    background: theme-get("bodyBgColor");
  }
}
a {
  text-decoration: none;
  @include theme() {
    color: theme-get("titleColor");
  }
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.container {
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
}
// .layout {
//   min-height: 100vh;
//   position: relative;
// }

h1 {
  font-size: 38px;
  line-height: 40px;
  font-weight: $semiBold;
  @include sm {
    font-size: 48px;
    line-height: 48px;
    letter-spacing: -2px;
  }
  @include md {
    font-size: 58px;
    line-height: 72px;
    letter-spacing: -2px;
  }
}
h3 {
  font-size: 14px;
  line-height: 18px;

  @include md {
    font-size: 18px;
  }
}
h2 {
  font-size: 18px;
  line-height: 18px;
  letter-spacing: 0px;

  @include md {
    font-size: 24px;
    line-height: 26px;
    letter-spacing: 0px;
  }
}
p {
  font-size: 15px;
  @include md {
    font-size: 16px;
  }
}

@include sm {
  .container {
    width: 570px;
  }
}
@include md {
  .container {
    width: 760px;
  }
}
@include lg {
  .container {
    width: 970px;
  }
}
@include xl {
  .container {
    width: 1180px;
  }
}
@include xxl {
  .container {
    width: 1340px;
  }
}
