.resume {
  padding-bottom: 170px;
  .box {
    display: flex;
    flex-wrap: wrap;
    position: relative;
    max-width: 1000px;
    margin: 0 auto;
    @include sm {
      &::before {
        content: "";
        position: absolute;
        width: 1px;
        height: 100%;
        background-color: $green;
      }
    }
    @include md {
      &::before {
        left: calc(50% - 1px);
      }
    }
  }
}
